::-webkit-scrollbar {
    width: 3px;
    height: 3px;
}
::-webkit-scrollbar-thumb {
    background-color: rgba(255, 255, 255, 0.3);
}
.to-do {
    &__item {
        border: 1px solid rgba(255, 255, 255, 0.7);
        box-shadow: 0 0 10px rgba(255, 255, 255, 0.3);
        background-color: #212121;
        position: relative;
        padding: 16px 16px;
        border-radius: 16px;
        height: 410px;
    }
    &__list.MuiList-root {
        flex: 1 1 auto;
        overflow-y: auto;
        padding: 0;
        margin-top: 16px;
        margin-bottom: 16px;
    }
}
